.slick-slider-hero__card {
  position: relative; }
  .slick-slider-hero__card img {
    width: 100vw;
    max-height: 90vh; }
  .slick-slider-hero__card__text {
    position: absolute;
    bottom: 7%;
    left: 5%;
    color: white; }
    .slick-slider-hero__card__text p {
      letter-spacing: 6px;
      text-transform: uppercase;
      margin-bottom: 4px; }
    .slick-slider-hero__card__text h1 {
      margin-bottom: 24px; }
    .slick-slider-hero__card__text a {
      display: block;
      color: white;
      transition: ease 0.25s;
      text-transform: uppercase;
      font-size: 12px;
      border: 1px solid white;
      text-align: center;
      padding: 0.6rem 1.5rem;
      width: 100%; }
    .slick-slider-hero__card__text a:hover {
      color: #c50000;
      background-color: white; }

.slick-slider-hero .slick-next,
.slick-slider-hero .slick-prev {
  position: absolute;
  width: 3rem;
  height: 3rem;
  z-index: 1; }

.slick-slider-hero .slick-next {
  position: absolute;
  left: 95%;
  top: 90%; }

.slick-slider-hero .slick-prev {
  position: absolute;
  left: 90%;
  top: 90%; }

.slick-slider-hero .slick-next:before,
.slick-slider-hero [dir='rtl'] .slick-prev:before {
  content: '→';
  color: #c50000;
  font-size: 40px; }

.slick-slider-hero .slick-prev:before {
  content: '←';
  color: #c50000;
  font-size: 40px; }

@media (min-width: 768px) {
  .slick-slider-hero .slick-slider {
    max-height: 90vh; } }

@media (max-width: 767px) {
  .slick-slider-hero .slick-next {
    display: none !important; }
  .slick-slider-hero .slick-prev {
    display: none !important; } }

.loading-animation {
  width: 100%;
  height: 100%;
  padding: 6rem 0;
  margin: 0 auto;
  text-align: center; }
  .loading-animation__icon .fa-spinner {
    width: 100px;
    height: 100px;
    margin: 0 auto;
    animation-name: loading-spinner;
    animation-duration: 1.3s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-out; }

@keyframes loading-spinner {
  from {
    transform: rotateZ(0deg); }
  to {
    transform: rotateZ(360deg); } }
